<template>
    <div>
        <ul class="gedan">
            <li v-for="song in ret" :key="song.id" class="gelan">
                <div>
                    <img :src="song.al.picUrl" alt="" style="width:50px;border-radius: 5px">
                    <span  class="mname">{{song.name}}</span>
                </div>
                <div class="el-icon-video-play but" @click="benye(song)">
                </div>
            </li>
        </ul>
        <div class="bofangqi" v-show="bfq">
            <img :src="byfengmian" alt="" @click="bofang(song)" style="cursor: pointer">
            <audio :src="bymp3" controls="controls" autoplay="autoplay" class="video"></audio>
        </div>
    </div>
</template>

<script>
    export default {
        name: "bofangqi",
        data(){
            return{
                bfq: false,
                byfengmian:"",
                bymp3:"",
                song:Object
            }
        },
        props:{
          ret:Array
        },
        methods:{
            //本页播放
            benye(song){
                this.bfq = true
                this.byfengmian = song.al.picUrl
                this.song = song
                this.$axios.get("https://api.imjad.cn/cloudmusic/",{
                    params:{
                        type: "song",
                        id: song.id,
                        search_type: 1
                    }
                }).then(res=>{
                    console.log(res)
                    this.bymp3 = res.data.data[0].url
                })
            },
            //播放器播放
            bofang(song){
                this.$axios.get("https://api.imjad.cn/cloudmusic/",{
                    params:{
                        type: "song",
                        id: song.id,
                        search_type: 1
                    }
                }).then(res=>{
                    this.$axios.get("https://api.imjad.cn/cloudmusic/",{
                        params:{
                            type: "lyric",
                            id: song.id,
                            search_type: 1
                        }
                    }).then(res2=>{
                        if(!res2.data.hasOwnProperty('lrc')){
                            res2.data.lrc = Object
                            res2.data.lrc.lyric = "该歌曲没有歌词"
                        }
                        this.$router.push({
                            name: "play",
                            params:{
                                thissong:res.data.data[0],
                                potho: song.al.picUrl,
                                geci: res2.data.lrc.lyric.split(/\[.*\]/),
                                mname: song.name
                            }
                        })
                    })
                }).catch(err=>{
                    console.log(err)
                })
            }
        }
    }
</script>

<style scoped>
    .gedan{
        width: 780px;
        margin: 10px auto;
        padding: 0;
        list-style: none;
        background: white;
    }
    .gelan{
        width: 760px;
        height: 50px;
        line-height: 50px;
        text-align: left;
        padding: 10px;
        display: flex;
        justify-content: space-between;
    }
    .mname{
        margin-left: 20px;
        top: -16px;
        position: relative;
    }
    .but{
        line-height: 50px;
        font-size: 35px;
        cursor: pointer;
    }
    .but:hover{
        color: red;
    }
    .bofangqi{
        position: fixed;
        top: 93%;
        background: #f1f3f4;
    }
    .bofangqi>img{
        width: 58px;
    }
    .bofangqi>audio{
        width: 782px;
    }
</style>
