<template>
  <div class="home">
    <el-input v-model="sousou" placeholder="输入歌曲名字/歌手/歌词" @change="sele" style="width: 50%;margin-top: 30px"></el-input>
    <Bofangqi :ret="ret"></Bofangqi>
  </div>
</template>

<script>
// @ is an alias to /src
import Bofangqi from "../components/bofangqi.vue"

export default {

  name: 'home',
  components:{
    Bofangqi
  },
  data(){
    return{
      sousou:"",
      ret:[]
    }
  },
  methods:{
    sele(){
      this.$axios.get("https://api.imjad.cn/cloudmusic/",{
        params:{
          type: "search",
          s: this.sousou
        }
      }).then(res=>{
        // console.log(res)
        if(!res.data.result.songs){
          alert("搜索结果为空！")
        }else {
          this.ret = res.data.result.songs
        }
        // console.log(this.ret)
      }).catch(err=>{
        console.log(err)
      })
    }
  }
}
</script>
<style scoped>
  .home{
    margin: 0 auto;
    width: 840px;
    padding-bottom: 30px;
  }
</style>
