import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/mains',
    name: 'mains',
    redirect:"/mains/gedan",
    component: ()=> import("../views/mains.vue"),
    children:[{
      path: 'gedan',
      name: 'gedan',
      component: ()=> import("../views/gedan.vue"),
    },
      {
        path: 'home',
        name: 'home',
        component: Home,
        props:true
      },
      {
        path: 'geshou',
        name: 'geshou',
        component: ()=> import("../views/geshou.vue"),
      },
      {
        path: 'diantai',
        name: 'diantai',
        component: ()=> import("../views/diantai.vue"),
      },
      {
        path: 'mv',
        name: 'mv',
        component: ()=> import("../views/mv.vue"),
      }]
  },

  {
    path: '/about',
    name: 'about',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  },
  {
    path: '/play',
    name: 'play',
    component: () => import( '../views/play.vue'),
    props:true
  },
  {
    path: '/gedanlist',
    name: 'gedanlist',
    component: () => import( '../views/gedanlist.vue'),
    props:true
  },
  {
    path: '/geshoulist',
    name: 'geshoulist',
    component: () => import( '../views/geshoulist.vue'),
    props:true
  },
  {
    path:"/",
    redirect:"/mains"
  }
]

const router = new VueRouter({
  // mode: 'history',
  // base: process.env.BASE_URL,
  routes
})

export default router
